<template>
  <div class="hello">
    <h1>Experiment 01</h1>

    <p>Purpose: Test if wrapping a getter in a function, will disable caching.</p>

    <pre>my_value1: {{my_value1}}</pre>
    <pre>my_value2: {{my_value2}}</pre>
    <pre>my_dep_value1: {{my_dep_value1}}</pre>
    <pre>my_dep_value2: {{my_dep_value2}}</pre>

    <button @click="SET_VALUE_A(Math.random())">Button A</button>
    <button @click="SET_VALUE_B(Math.random())">Button B</button>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'

// TODO: Use store1.js

export default {
  name: 'HelloWorld',
  computed: {
    ...mapGetters([
      'my_value1',
      'my_value2',
      'my_dep_value1',
      'my_dep_value2',
    ]),
  },
  methods: {
    ...mapMutations([
      'SET_VALUE_A',
      'SET_VALUE_B',
    ]),
  },
  props: {
    msg: String
  }
}
</script>

